export default {
	data() {
		return {
			city1: '',
			city2: '',
			choiceDate: '',
			list: [],
			index1: 0
		}
	},
	computed: {
		date1() {
			return this.$store.state.date2
		},
		day1() {
			return this.$store.state.day2
		}
	},
	onLoad(option) {
		//刷新保留用户uid
		if (this.getRequest('uid')) {
			var uids = this.getRequest('uid')
			this.$store.commit('changeUid', uids)
		}
		this.choiceDate = option.date
		this.index1 = option.index1
		let listData = JSON.parse(option.list)
		this.list = listData[option.index]
		this.$store.commit('changeListEnd', JSON.stringify(listData[option.index]))
	},
	onReady() {
		let city1 = this.$store.state.city1
		let city2 = this.$store.state.city2
		this.city1 = city1
		this.city2 = city2
	},
	methods: {
		getRequest(variable) {
			var query = window.location.search.substring(1);
			var vars = query.split("&");
			for (var i = 0; i < vars.length; i++) {
				var pair = vars[i].split("=");
				if (pair[0] == variable) {
					return pair[1];
				}
			}
			return (false);
		},
		back() {
			uni.navigateBack({
				delta: 1
			})
		},
		goOrder(k) {
			uni.navigateTo({
				url: '../doubleOrder/doubleOrder?index2=' + k + '&index1=' + this.index1
			})
		}
	}
}
